import React, { Component } from 'react';
import styled from 'styled-components';
import Theme from '../../theme';
import { remCalc } from '@lux/helpers';

import apple from '../../assets/images/promo-banner.png';
import fetch from '../../assets/images/promo-fetch.png';
import sport from '../../assets/images/promo-sport.png';

import { Grid, Row, Column, Heading, OptusButton } from '../../index';

const BannerWrapper = styled.div`
    background: rgb(255, 255, 255);

    background-attachment: scroll;
    background-size: cover;
    background-position: 50% 80%;
    background-repeat: no-repeat;
    width: 100%;
    display: block;
    z-index: 0;
    transform: scale3d(1.3,1.3,1.3) translateZ(0);
    backface-visibility: hidden;
    filter: none;

    transform: scale(1,1);

    background-image: url(${apple});
    background-image: url(${props =>
        props.background === 'apple' ? apple : props.background === 'fetch' ? fetch : sport});

    padding-top: ${props =>
        props.headerSpace ? "160px" : "60px"};
    padding-bottom: 150px;

    color: rgb(255, 255, 255);

    @media only screen and (max-width: 852px) {
        text-align: center;
    }
`;

const StackedWrapper = styled.div`
    background: ${props => props.color ? props.color : "rgb(255, 255, 255)"};
    padding: ${props => props.condensed ? "40px 0 20px" : "100px 0 100px"};

    @media only screen and (max-width: 852px) {
        text-align: center;
    }
`;

const StackedImage = styled.img`
    width: 100%;
    min-height: 360px;
`;

const StackedCentre = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
`;
const StackedDescription = styled.p`
    color: rgb(255, 255, 255);
    font-size: 24px;
    line-height: 1.2;
`;
const StackedDisclaimer = styled.p`
    color: rgb(255, 255, 255);
    font-size: 16px;
    line-height: 1.2;
    margin-bottom: 60px;
`;

const BannerHeading = styled.h1`
    color: rgb(255, 255, 255);
    font-size: 48px;
    line-height: 1.2;
    letter-spacing: 0;
    @media only screen and (max-width: 852px) {
        font-size: 40px;
    }
`;
const BannerDescription = styled.p`
    color: rgb(255, 255, 255);
    font-size: 24px;
    line-height: 1.2;
`;
const BannerDisclaimer = styled.p`
    color: rgb(255, 255, 255);
    font-size: 16px;
    line-height: 1.2;
    margin-bottom: 60px;
`;

class Banner extends Component {
  render() {
    if (this.props.type === 'banner') {
        return (
            <BannerWrapper {...this.props}>
                <Grid>
                    <Row>
                        <Column sm={12} md={12} lg={6}>
                            <BannerHeading>{this.props.heading}</BannerHeading>
                            <BannerDescription>{this.props.description}</BannerDescription>
                            <BannerDisclaimer>{this.props.disclaimer}</BannerDisclaimer>
                            <OptusButton>{this.props.ctaText}</OptusButton>
                        </Column>
                    </Row>
                </Grid>
            </BannerWrapper>
        );
    }

    if (this.props.type === 'stacked') {
        return (
            <StackedWrapper {...this.props}>
                <Grid>
                    <Row>
                        <Column sm={12} md={12} lg={6}>
                            <BannerHeading>{this.props.heading}</BannerHeading>
                            <StackedDescription>{this.props.description}</StackedDescription>
                            <StackedDisclaimer>{this.props.disclaimer}</StackedDisclaimer>
                            {this.props.ctaText && (<OptusButton>{this.props.ctaText}</OptusButton>)}
                        </Column>
                        {this.props.background && (
                            <Column sm={12} md={12} lg={6}>
                                <StackedCentre>
                                    <StackedImage src={this.props.background === 'fetch' ? fetch : this.props.background === 'sport' ? sport : ''} />
                                </StackedCentre>
                            </Column>
                        )}
                    </Row>
                </Grid>
            </StackedWrapper>
        );
    }
  }
}

export default Banner;
